import React from "react";
import Users from "./Users";
import {connect} from "react-redux";
import {follow, getUsers, setActivePage, setTermSearch, unFollow} from "../../Redux/usersReducer";
import Preloader from "../../Utilits/Preloader/Preloader";
import {setActivePageSite} from "../../Redux/authReducer";
import withRedirect from "../../Utilits/Redirect";
import {compose} from "redux";

class UsersContainer extends React.Component {
    componentDidMount() {
        this.props.getUsers(this.props.activePage,this.props.pageSize, this.props.termSearch);
        this.props.setActivePageSite("USERS");
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.activePage !== this.props.activePage)
            this.props.getUsers(this.props.activePage,this.props.pageSize, this.props.termSearch);
        if (prevProps.termSearch !== this.props.termSearch)
            this.props.getUsers(this.props.activePage,this.props.pageSize, this.props.termSearch)

    }

    onSubmit = (formData) => {
        this.props.getUsers(this.props.activePage,this.props.pageSize, formData.term)
    };

    render() {
        return (
            <>
                {this.props.isPreloader ? <Preloader/> : null}
                <Users users={this.props.users}
                       totalCount={this.props.totalCount}
                       activePage={this.props.activePage}
                       setActivePage={this.props.setActivePage}
                       pageSize={this.props.pageSize}
                       follow={this.props.follow}
                       unFollow={this.props.unFollow}
                       waitingSubscripe={this.props.waitingSubscripe}
                       termSearch={this.props.termSearch}
                       setTermSearch={this.props.setTermSearch}
                       onSubmit={this.onSubmit}
                />
            </>
        );
    }
}

let mapStateToProps = (state) => {
    return {
        users: state.usersPage.users,
        totalCount: state.usersPage.totalCount,
        activePage: state.usersPage.activePage,
        pageSize: state.usersPage.pageSize,
        waitingSubscripe: state.usersPage.waitingSubscripe,
        isPreloader: state.usersPage.isPreloader,
        termSearch: state.usersPage.termSearch
    }
};
export default compose(
    connect(mapStateToProps, {getUsers, setActivePage, follow, unFollow, setTermSearch, setActivePageSite}),
    withRedirect
)(UsersContainer)
